import React from 'react';
import { cssDecorator } from '@front-toolkit/utils';

import { LabelFontColor, LabelSize } from './types';

import styles from './styles.scss';

const cd = cssDecorator(styles);

export interface LabelProps {
  /** Тип лэйбла */
  size?: LabelSize;

  /** Цвет шрифта */
  color?: LabelFontColor;

  /** Атрибут связи с элементом формы */
  htmlFor?: string;

  /** ID для тестирования */
  dataTestId?: string;

  /** Дочерние элементы */
  children: React.ReactNode;

  /** Флаг для наследования цвета текста от родителя */
  inheritColor?: boolean;

  /** Имя тега (при заполненном htmlFor будет игнорироваться) */
  tagName?: 'div' | 'span';

  /** Флаг для отрисовки строчного представления */
  inline?: boolean;
}

const Label: React.FC<LabelProps> = ({
  size = 'small',
  color = 'primary',
  tagName = 'label',
  htmlFor,
  children,
  inline,
  dataTestId,
  inheritColor = false
}) => {
  const labelClasses = cd(
    'label',
    {
      [`size_${ size }`]: true,
      [`color_${ color }`]: !inheritColor,
      inline
    }
  );

  return React.createElement(
    htmlFor ? 'label' : tagName,
    {
      'data-test-id': dataTestId,
      htmlFor,
      className: labelClasses
    },
    children
  );
};

const MemoizedLabel = React.memo(Label);

export { Label };

export default MemoizedLabel;
